import ProductImageUpload from "./ProductImageUpload";
import { supabase } from "@/utils/supabase/supabase"

// Типизация категории
type Category = {
  name: string;
};

// Типизация продукта с учетом join запроса
type Product = {
  id: number;
  name: string;
  main_image: string | null;
  categories: Category | null; // одиночный объект или null для foreign key relation
};

export default async function Admin() {
  const { data: products } = await supabase
    .from("products")
    .select(`
      id, 
      name, 
      main_image,
      categories(name)
    `)
    .order("id", { ascending: true })
    .returns<Product[]>() // явная типизация возвращаемых данных

    
    const pWithoutImage = products?.filter(item => !item.main_image);
    const pWithImage = products?.filter(item => item.main_image);

return (
    <div className="p-6">
      
      {/* Продукты БЕЗ изображений */}
      {pWithoutImage!.length > 0 && (
        <div className="mb-8">
          <h2 className="text-2xl font-bold mb-4 text-red-600">
            Требуется загрузка изображений ({pWithoutImage!.length})
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {pWithoutImage!.map((item) => (
              <div key={item.id} className="p-4 border-2 border-red-300 bg-red-50 rounded-lg"> 
                <div className="bg-gray-200 border-2 border-dashed rounded-xl w-full h-48 flex items-center justify-center mb-3">
                  <span className="text-gray-500">No image</span>
                </div>

                <div className="space-y-1 text-gray-600">
                  <div className="text-sm ">ID: {item.id}</div>
                  <div className="font-semibold">{item.name}</div>
                  <div className="text-sm ">
                    Category: {item.categories?.name ?? 'No category'}
                  </div>

                  <ProductImageUpload
                    product_id={item.id}
                    category_name={item.categories?.name ?? ''}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Продукты С изображениями */}
      {pWithImage!.length > 0 && (
        <div>
          <h2 className="text-2xl font-bold mb-4 text-green-600">
            Изображения загружены ({pWithImage!.length})
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {pWithImage!.map((item) => (
              <div key={item.id} className="p-4 border border-green-300 bg-green-50 rounded-lg"> 
                <img 
                  src={item.main_image!} 
                  alt={item.name} 
                  className="w-full h-48 object-cover rounded mb-3" 
                />

                <div className="space-y-1">
                  <div className="text-sm text-gray-600">ID: {item.id}</div>
                  <div className="font-semibold">{item.name}</div>
                  <div className="text-sm text-gray-600">
                    Category: {item.categories?.name ?? 'No category'}
                  </div>

                  <ProductImageUpload
                    product_id={item.id}
                    category_name={item.categories?.name ?? ''}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

    </div>
  )}